input[type="range"] {
  -webkit-appearance: none;
  margin: 21px 0;
  margin-bottom: 5px;
  width: 100%; }

input[type="range"]:focus {
  outline: none; }

input[type="range"]::-webkit-slider-runnable-track {
  width: 100%;
  height: 1px;
  cursor: pointer;
  background: #c1c2ca; }

input[type="range"]::-webkit-slider-thumb {
  height: 14px;
  width: 14px;
  border: none;
  border-radius: 50%;
  background: #2c36f2;
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -6px; }

input[type="range"]:focus::-webkit-slider-runnable-track {
  background: #c1c2ca; }

input[type="range"]::-moz-range-track {
  width: 100%;
  height: 1px;
  cursor: pointer;
  background: #c1c2ca; }

input[type="range"]::-moz-range-thumb {
  height: 14px;
  width: 14px;
  border: none;
  border-radius: 50%;
  background: #2c36f2;
  cursor: pointer; }

input[type="range"]::-ms-track {
  width: 100%;
  height: 1px;
  cursor: pointer;
  background: transparent;
  border-color: transparent;
  border-width: 0 0;
  color: transparent; }

input[type="range"]::-ms-fill-lower {
  background: #c1c2ca; }

input[type="range"]::-ms-fill-upper {
  background: #c1c2ca; }

input[type="range"]::-ms-thumb {
  height: 14px;
  width: 14px;
  border: none;
  border-radius: 50%;
  background: #2c36f2;
  cursor: pointer; }

input[type="range"]:focus::-ms-fill-lower {
  background: #c1c2ca; }

input[type="range"]:focus::-ms-fill-upper {
  background: #c1c2ca; }
